/* || RESET */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* || VARIABLES */

:root {

  /* COLORS */
  --BGCOLOR-FADE: rosybrown;
  --BGCOLOR: firebrick;
  --BGIMAGE: linear-gradient(to bottom, var(--BGCOLOR), var(--BGCOLOR-FADE));
  --BODY-BGCOLOR: #FFF;
  --FOOTER-BGCOLOR: rgb(206, 221, 221);
  ;
  --BORDER-COLOR: firebrick;
  --FONT-COLOR: #000;


  /* BORDERS */
  --BORDERS: 1px solid var(--BORDER-COLOR);
  --BORDER-RADIUS: 15px;
}


/* || UTILITY CLASSES */

.nowrap {
  white-space: nowrap;
}

.center {
  text-align: center;
}

.grid-center {
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}

.justify {
  text-align: justify;
}

.no-margin {
  margin: 0;
}

.first-character {
  float: left;
  font-family: Georgia;
  font-size: 75px;
  line-height: 40px;
  padding-top: 4px;
  padding-right: 8px;
  padding-left: 3px;
}

