/* || rough work for development stage to highlight element boxes */
.yellow {
  background-color: yellow;
}

.red {
  background-color: red;
}

/* || GENERAL STYLES */

html {
  scroll-behavior: smooth;
  font-size: var(--FS);
  font-family: var(--FF);
  background-color: var(--BGCOLOR);
  background-image: var(--BGIMAGE);
}

body {
  background-color: var(--BODY-BGCOLOR);
  color: var(--FONT-COLOR);
  min-height: 100vh;
  max-width: 1000px;
  margin: 0 auto;
  border-left: var(--BORDERS);
  border-right: var(--BORDERS);
  box-shadow: 0 0 10px var(--BORDER-COLOR);
    padding-top: 100px;
  
}

a {
  text-decoration: none;
}

.active-link:hover {
  color: var(--BORDER-COLOR)
}

.wrapper--main{
  scroll-margin-top: 1rem;
}

/* || HEADER */
.header__logo {
  width: 35px;
  height: 20px;
  margin: 0 0.5em 0.7em;
  border-radius: 15%;
}

/* || HOME */
/* HERO*/
.jumbotron {
  min-height: 100vh;
}


/* CAROUSEL */
.carousel-item>img {
  width: 75%;
  height: 500px;
  margin: auto;
  object-fit: contain;
  border-radius: 100%;
}

/* TEAM SECTION */
.team-card {
  border-top: 15px solid var(--BORDER-COLOR);
  border-left: 11px solid var(--BORDER-COLOR);
  width: 200px;
  height: 300px;
  border-radius: 5%;
}

.team-card>img {
  width: 200px;
  height: 200px;
  border-radius: 0% 3% 3% 0%;
}

/* || SERVICES */
.services__card {
  width: 300px;
  margin: 0 auto;
}

.services__card>img {
  padding: 0;
  width: 250px;
  height: 180px;
  max-width: 85vw;
  margin: 0 auto;
  border-radius: 2%;
}

/* || ABOUT */
.team__img--lg,
.founders img,
.branches img {
  border-radius: 5%;
  margin: 0 auto;
  max-width: 90vw;
}

/* FOUNDERS */
.founders img {
  width: 300px;
  height: 250px;
}

/* TEAM */
.team__img--lg {
  height: 250px;
  width: 400px;
}

/* BRANCHES */
.branches img {
  height: 300px;
  width: 400px;
}

/* || CONTACT */


/* || FOOTER */
.footer {
  background-color: var(--FOOTER-BGCOLOR);
}